import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";

import { Menu, Home, About } from "./components";

function App() {
  return (
    <Router>
      <div>
        <Menu />

        <Route exact path="/camportfolio/" component={Home} />
        <Route exact path="/camportfolio/about" component={About} />
      </div>
    </Router>
  );
}

export default App;
